<template>
  <div class="goods">
    <van-nav-bar title="商机管理" :fixed="true"/>
    <div style="height: 12.26667vw;"></div>
    <van-dropdown-menu active-color="#1989fa" :fixed="true">
      <van-dropdown-item v-model="value1" :options="option1" @change="selectActon"/>
      <van-dropdown-item v-model="value2" :options="option2" @change="selectActon"/>
    </van-dropdown-menu>

    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div style="padding:10px;border-bottom: 1px dotted #e0e0e0;" v-for="(item,index) in list" :key="index">
        <div
          style="padding:10px 0px;display: flex;align-items: center;justify-content: space-between"
        >
          <div style="display: flex;align-items: center;">
            <!-- <img
              style="width: 40px;height: 40px;border-radius: 20px;"
              src="https://img.yzcdn.cn/vant/tree.jpg"
            /> -->
             <div style="font-size: 13px;background:#8ea0f2;color:white;width: 40px;height: 40px;border-radius: 20px;line-height:40px;text-align:center;">v2.0</div>
            <div style="margin-left: 10px;">
              <div style="color: #7e7e7e;font-size: 15px;">{{item.name}}</div>
              <div
                style="margin-top: 10px;color: #808080;font-size: 14px;max-width:180px;"
              >{{item.username}}: {{item.mobile}}</div>
            </div>
          </div>
          <div style="width:66.5px;text-align:left;">
            <div style="color: #7a7a7a;font-size: 13px;">{{item.create_time|_GetDateStr}}</div>
            <div
              style="color: #a3cfa8;font-size: 13px;margin-top: 14px;"
              v-if="item.status=='dealed'"
            >已成交</div>
            <div
              style="color: #1989fa;font-size: 13px;margin-top: 14px;"
              v-else-if="item.status=='running'"
            >跟进中</div>
            <div
              style="color: #ff976a;font-size: 13px;margin-top: 14px;"
              v-else-if="item.status=='waiting'"
            >待跟进</div>
            <div
              style="color: #ee0a24;font-size: 13px;margin-top: 14px;"
              v-else
            >已丢失</div>
          </div>
        </div>
        <!-- 操作 -->
        <div style="display: flex;align-items: center;justify-content: flex-end;">
             <div>
                 <van-button type="info" size="mini" v-if="item.status == 'waiting' && !is_admin" @click="receiveTap(item)">领取确认</van-button>
                 <van-button type="info" size="mini" v-if="item.status == 'running' && !is_admin" @click="continue_follow(item)">继续跟进</van-button>
                 <van-button type="primary" size="mini" @click="genjiLogTap(item)">跟进记录</van-button> 
             </div>
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>

    <van-tabbar v-model="active" :border="true">
       <van-tabbar-item icon="gem-o" to="/">商机管理</van-tabbar-item>
       <van-tabbar-item icon="todo-list-o" to="/todayTask">今日任务</van-tabbar-item>
       <van-tabbar-item icon="browsing-history-o" to="/commonSea">公海</van-tabbar-item>
       <van-tabbar-item icon="user-o" to="/user">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {
  Tabbar,
  TabbarItem,
  Toast,
  NavBar,
  DropdownMenu,
  DropdownItem,
  List,
  Button,
  Dialog
} from "vant";

export default {
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
    [NavBar.name]: NavBar,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [List.name]: List,
    [Button.name]:Button,
    [Dialog.name]:Dialog
  },
  filters: {
    _GetDateStr(sj_str) {
      var iday = 0;
      try {
        var sjdate = new Date(sj_str).toLocaleDateString();
        var sjdatedd = Date.parse(sjdate) / 1000;
        if (sjdatedd == isNaN) {
          return "未知";
        }
        // eslint-disable-next-line no-console
        console.log(sjdatedd);
        var date = new Date().toLocaleDateString();
        var dddate = Date.parse(date) / 1000;
        iday = Math.floor(parseInt(dddate - sjdatedd) / 60 / 60 / 24);
      } catch (err) {
        return "未知";
      }

      if (-1 == iday) {
        return "今天";
      } else if (0 == iday) {
        return "昨天";
      } else {
        var dd1 = [];
        try {
          var dd2 = new Date(sj_str).toLocaleDateString();
          if (dd2 == "Invalid Date") {
            return "未知";
          }
          dd1 = dd2.split("/");
          // eslint-disable-next-line no-console
          console.log("dsfsdf");
          // eslint-disable-next-line no-console
          console.log(dd1);
        } catch (err) {
          return "未知";
        }

        var y = dd1[0];
        var m = dd1[1]; //获取当前月份的日期
        var d = dd1[2];
        return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
      }
    },
  },
  data() {
    return {
      value1: "",
      value2: "",
      option1: [{ text: "所有意向等级", value: "" }],
      option2: [
        { text: "所有跟进状态", value: "" },
        { text: "跟进中", value: "running" },
        { text: "已完成", value: "finish" },
        { text: "已丢失", value: "failed" },
        { text: "待跟进", value: "waiting" },
      ],
      list: [],
      loading: false,
      finished: false,
      active: 0,
      page: 1,
      pageSize: 20,
      is_depart_admin:false,
      is_admin:false
    };
  },
  created() {
     let isAdmin           = window.localStorage.getItem("is_admin");
      this.is_admin        = isAdmin>0 ? true:false;
      let isDepartAdmin     = window.localStorage.getItem("is_depart_admin");
      this.is_depart_admin  = isDepartAdmin>0 ? true:false;
    this.findSerachList();
  },
  methods: {
    continue_follow(item){
       this.$router.push({path:"/addGenJi",query:{id:item.customer_id}});
    },
    receiveTap(item){

      Dialog.confirm({
        title: '提示',
        message: '确认领取商机?',
      }).then(() => {
          // on confirm
                this.$http
              .post("/admin/business/confirm", {
                customer_id:item.customer_id
              })
              .then(function (response) {
                // eslint-disable-next-line no-console
                console.log(response.data);
                if (response.data && response.data.code === 2000) {
                  //   // 数据全部加载完成purpose  source
                    Toast("领取成功!");
                }else{
                    Toast("领取失败!");
                }
              })
              // eslint-disable-next-line no-unused-vars
              .catch(function (error) {
                  Toast("网络错误!");
              }); 
              })
        .catch(() => {
          // on cancel
        });
    },
    genjiLogTap(item){
        this.$router.push({path:"/genjiLog",query:{id:item.customer_id}});
    },
    // eslint-disable-next-line no-unused-vars
    selectActon(value){
       this.finished = false;
       this.page = 1;
       this.loading  = false;
       this.list = [];
       // eslint-disable-next-line no-console
       console.log("dasdas");
       this.onLoad();
    },
    findSerachList() {
      let that = this;
      this.$http
        .post("/admin/business/get_type", {})
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            //   // 数据全部加载完成purpose  source
            if (Array.isArray(response.data.data.purpose)) {
              that.option1 = response.data.data.purpose.map((item) => {
                return {
                  text: item.desc,
                  value: item.type,
                };
              });
              that.option1.unshift({ text: "所有意向等级", value: "" });
            }

            // if (Array.isArray(response.data.data.source)) {

            //   that.option2 = response.data.data.source.map((item) => {
            //     return {
            //       text: item.type_desc,
            //       value: item.id,
            //     };
            //   });

            //   that.option2.unshift({text:"所有跟进状态",value:""});
            // }
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {});
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      let that = this;
      // eslint-disable-next-line no-console
      console.log("submit");
      this.$http
        .post("/admin/business/lists", {
          page_nums: 20,
          page: this.page,
          status: this.value2,
          type: this.value1,
          keywords: "",
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            //   // 数据全部加载完成
            that.list = that.list.concat(response.data.data.list);
            // eslint-disable-next-line no-console
            console.log(that.list);
            if (response.data.data.list.length < 20) {
              that.finished = true;
            }
          }else{
              that.finished = true;
          }
          that.loading = false;
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          that.loading = false;
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          // Toast("登录失败");
        });

      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1);
      //   }
      //   // 加载状态结束
      //   this.loading = false;

      //   // 数据全部加载完成
      //   if (this.list.length >= 40) {
      //     this.finished = true;
      //   }
      // }, 1000);
    },
    formatPrice() {
      return "¥" + (this.goods.price / 100).toFixed(2);
    },
    onClickCart() {
      this.$router.push("cart");
    },
    sorry() {
      Toast("暂无后续逻辑~");
    },
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      Toast("按钮");
    },
  },
};
</script>

<style lang="less">
.goods {
  height: 100vh;
  box-sizing: border-box;
  padding-bottom: 50px;
}
</style>
